/*	===========================================
	House List
	===========================================   */


	article.houses {


		position:relative;
		//color:$yellow-footer;
		background: $white-bg;
		box-shadow: 0 0 10px 0 $brown-light;
		box-sizing: border-box;
		padding:12px;
		//text-align: center;
		margin-bottom:2%;
		margin-left:2%;
		width:32%;
		float:left;
		&:hover {
			background: white;
		}

		&:nth-of-type(3n+1) {
			margin-left:0;
			clear:left;
		}

		@include breakpoint($largeBreak){
			width:48%;
			margin-left: 4%;
			margin-bottom:4%;
			&:nth-of-type(3n+1) {
				margin-left:4%;
				clear:none;
			}

			&:nth-of-type(2n+1) {
				margin-left:0;
				clear:left;
			}

		}
		@include breakpoint($tabletBreak){
			width:49%;
			margin-left: 2%;
			margin-bottom:2%;
			&:nth-of-type(3n+1) {
				margin-left:2%;
				clear:none;
			}

			&:nth-of-type(2n+1) {
				margin-left:0;
				clear:left;
			}

		}

		@include breakpoint($mobileBreak){
			width:auto;
			margin-left: 0;
			float:none;
			margin-bottom: 2em;
			&:nth-of-type(3n+1) {
				margin-left:0;
				clear:none;
			}

			&:nth-of-type(2n+1) {
				margin-left:0;
				clear:none;
			}

		}






	.house-figure {
		margin:0;
		// text-align: center;
		// width:50%;
		// margin:0 auto;
		width:42%;
		margin:0 4% 0 0;
		float:left;
		@include breakpoint($largeBreak){
			width:36%;
		}
		@include breakpoint($tabletBreak){

		}

		// &:after{
		// 	text-align: center;
		// 	content:"&";
		// 	color:$orange-link;
		// 	font-family: $displayFontFamily;
		// 	font-size: 2em;
		// 	display: block;
		// 	padding-top:0.75em;
		// }

		img {
			border:2px solid $brown-menu;
			box-sizing: border-box;
			box-shadow: 0 0 5px 0 $brown-light;
			&:hover {
				border:2px solid $brown-light;
			}

		}




	}
	.house-data {
		width:54%;
		float:right;

		padding-bottom:1.25em;

		text-align: left;


		@include breakpoint($largeBreak){
			width:60%;
		}
		@include breakpoint($tabletBreak){

		}

	}

	.house-readiness {
		background: $gray-footerold;
		//position: absolute;
		//bottom:0.75em;
		//right:0.75em;
		display:block;
		//text-align: right;
		margin-top:-0.25em;
		margin-bottom:0.25em;
		color:$white-bg;
		display: inline;
		padding:1px 0.25em;
		font-size:1em;
		font-family: $scriptFontFamily;
		font-style: normal;
		a {
					color:$brown-slogan;
		}
	}

	.house-title {
		position:relative;
		margin:0 0 0.5em;
		padding:0.45em 0 0.4em 0;
		font-size:1.5em;
		line-height:1.125em;
		text-align: center;
		text-transform: uppercase;
		background-color: $brown-menu;
		&:before {
			position:absolute;
			content:"";
			left:0px;right:0px;
			top:2px;
			bottom:2px;
			border-top:1px solid $yellow-footer;
			border-bottom:1px solid $yellow-footer;

		}


		a {

			color:$yellow-footer;

			position:relative;
			z-index:1;
			&:hover{
				text-decoration: none;
				color:$white-bg;
			}
		}



	}
	.house-subtitle {

		font-family: $displayFontFamily;
		text-align: left;
		//color:$brown-slogan;
		text-transform: none;
		//font-size: 1.5em;
		font-weight:bold;
		line-height:1.25em;
		font-size:1em;
		margin:0 0;

	}


	.action{
		position:absolute;
		right:0.75em;
		bottom:0.75em;
		font-family: $serifFontFamily;
		//font-style:italic;
		font-size: 0.875em;
		font-weight: bold;


	}
	}



/*	===========================================
	Featured House
	===========================================   */

.feat-houses {
	@extend article.houses;
	margin-bottom:0;
	margin-top:0em;

	//background: none;
	//color:$yellow-footer;
	//box-shadow: none;

	.house-data{


		&:after {
			content:"";
		}
	}
}

.feathouseblockwrap {
	//background: $gray-footerold;
	//margin-bottom:10em;


	.feathouseblock {
		margin-top: 5em;
		margin-bottom:3em;
		text-align: center;


		.featblock-title {
				display:none;


		}


		.feat-houses {
			@include breakpoint($mobileBreak){
				margin-bottom:1.5em;
			}
		}
		.feat-houses:nth-of-type(3n){
			@include breakpoint($largeBreak){
				display:none;
			}
			@include breakpoint($mobileBreak){
				display:block;
				margin-bottom:0;
			}
		}


	}

	.fblockmore {
	text-align: center;
	margin:1em 1em -1em;
	display: block;
	font-size: 1.25em;
	font-weight:bold;
	font-style: italic;

	}


}




/*------------ Sidebar House --------*/
.side-houses {
	@extend article.houses;

	width:auto;
	float:none;
	margin:0 0 1em 0;

	h3.house-subtitle {
		text-align:left;
	}
}
.sidehousemore {
	text-align: center;
	margin:1em 1em 3em;
	display: block;
	font-size: 0.875em;
	font-weight:bold;
	font-style: italic;

}

