.okos[class*="span"] {

	@include breakpoint($largeBreak){
		width: 5*$fluidGridColumnWidth+4*$fluidGridGutterWidth;
	}

	@include breakpoint($desktopBreak){
		width: 6*$fluidGridColumnWidth+5*$fluidGridGutterWidth;
	}

	@include breakpoint($tabletBreak){
		width: 6*$fluidGridColumnWidth+5*$fluidGridGutterWidth;
		float:left;
	}
	@include breakpoint($mobileBreak){
		float:none;
		width: 100%;
		display: block;
		box-sizing: border-box;
	}



}

.sidebar-1[class*="span"]{

	@include breakpoint($largeBreak){
		width: 3.5*$fluidGridColumnWidth+2.5*$fluidGridGutterWidth;
	}

	@include breakpoint($desktopBreak){
	    //float: none;
	    float:right;
	    //display: block;
	    width: 6*$fluidGridColumnWidth+5*$fluidGridGutterWidth;
	    margin-left: $fluidGridGutterWidth;
	   	//box-sizing: border-box;
	}

	@include breakpoint($tabletBreak){
	    //float: none;
	    float:right;
	    //display: block;
	    width: 6*$fluidGridColumnWidth+5*$fluidGridGutterWidth;
	    margin-left: $fluidGridGutterWidth;
	   	//box-sizing: border-box;
	}

	@include breakpoint($mobileBreak){
		float:none;
		width: 100%;
		display: block;
		margin-left: 0;
		box-sizing: border-box;
	}
}

.sidebar-2[class*="span"] {
	@extend .sidebar-1[class*="span"];

	@include breakpoint($largeBreak){
		//margin-left:7/8*$fluidGridGutterWidth;
	}
}

