.tothetop {
	text-align: center;
	text-transform: uppercase;
	display: inline-block;
	float:right;
	font-size:1.5em;

	margin-top:-2em;
	margin-right:-2em;
	background: $orange-menu;
	color:$white-bg;
	//font-family: $scriptFontFamily;
	//font-weight: bold;
	width:2em;
	height:2em;
	padding-top:0.475em;
	box-sizing: border-box;
	border-radius: 1em;
	&:hover {
		text-decoration:none;
		background: $orange-top;
		color:$white-darkbg;
	}

	@include breakpoint($tabletBreak){
		float:none;
		display:block;
		margin:1em auto 0 auto;
		//width:80px;
		//height:80px;
		font-size:3em;

	}
	i{

		//display: block;
	}
}

.topi {
	//@extend .tothetop;
	//float:none;
	text-align: center;
	display: block;
	font-size:1.5em;
	background: $orange-menu;
	color:$white-bg;
	box-sizing: border-box;
	border-radius: 1em;
	width:2em;
	height:2em;
	padding-top:0.475em;
	position:absolute;
	z-index: 2;
	right:1em;
	bottom:1em;


	@include breakpoint($tabletBreak){
		display:none;
		position: static;
		margin:0 auto 0 auto;
		font-size:3em;

	}
	&:hover {
		text-decoration:none;
		background: $orange-top;
		color:$white-darkbg;
	}
}

