/* ==========================================================================
   Base layout
   ========================================================================== */

body {
	padding-top: 40px;

	@include breakpoint($tabletBreak){
		padding-top:0;
	}
}
.feathouseblockwrap {
	background-image: linear-gradient(0deg, $white-bg, $white-darkbg);
	padding:0 0 1em;
	position:relative;
	background: url(../images/fogle.png) repeat-x center top $white-bg;

}
.nagywrap {
	background-image: linear-gradient($white-bg, $white-darkbg);
	overflow:hidden;
}

.kwrap-1 {

	background: url(../images/aljataj.png) repeat-x center bottom transparent;
	//background-image: linear-gradient($white-bg, $white-darkbg);
	padding-bottom:8em;
}


.home .kwrap-1, .page-template-template-contact .kwrap-1 {
	//background-image: none;
	background: url(../images/pitypang.png) repeat-x center bottom transparent;
	//background-image: none;
	//@include background(linear-gradient($white-bg, $white-darkbg));
	padding:4em 0 5em;
}

.home .kwrap-2,  {
	background: url(../images/pitypang.png) repeat-x center bottom transparent;
	//@include background(linear-gradient($white-bg, $white-darkbg));
	padding:0 0 4em 0;

}

.page-template-template-contact .kwrap-2,  {
	background: url(../images/aljataj.png) repeat-x center bottom transparent;
	//background-image: linear-gradient($white-bg, $white-darkbg);
	padding:2em 0 8em 0;
}


.page-id-15 .kwrap-1, .page-id-921 .kwrap-1{
 	//background-image: none;
 	background: url(../images/pitypang.png) repeat-x center bottom transparent;
 	padding-bottom:6em;
 }

.page-id-15 .kwrap-2, .page-id-921 .kwrap-2  {
	background: url(../images/aljataj.png) repeat-x center bottom transparent;
	//background-image: linear-gradient($white-bg, $white-darkbg);
	padding:2em 0 8em;
	background:none;
}
.page-id-15 .nagywrap, .page-id-921 .nagywrap  {
	background: url(../images/aljataj.png) repeat-x center bottom transparent;
	//background-image: linear-gradient($white-bg, $white-darkbg);
	padding:0 0 8em;
	background:none;
}

body.page-id-15, body.page-id-921 {
 	background: url(../images/feszek_bg.jpg) no-repeat center bottom $white-bg;
 	//@include background-size(100%);
 	//background-attachment: fixed;

 	//padding-bottom:15em;
}
.container-fluid {
	max-width: 1170px;
	margin-left: auto;
	margin-right:auto;
}


.main {
	ul:not(.nav), ol {
		li {
			margin:1em 2em;
		}
	}
}
.post-nav {
	@include clearfix();
	clear:both;
}
.row-fluid.narrow {
	max-width: $tabletBreak;
	margin-left:auto;
	margin-right: auto;
}