.headinfo {
	margin-top:1em;
	margin-bottom:2.5em;
	@include breakpoint($tabletBreak) {
		margin-bottom:1.5em;
	}
}
.fixblock {
	margin-left:230px;
	margin-bottom:1.5em;
	@include breakpoint($desktopBreak) {
		margin-left:155px;
	}
	@include breakpoint($tabletBreak) {
		margin-left:0px;
	}
}



.langsel {
	font-weight: bold;
	float:left;
}

#lang_sel_list {


	li {
		float:right;
		list-style-type: none;
		//font-weight: normal;
		text-transform: uppercase;
		padding-right:0.33333em;
		&:after {
			content:"|";


		}
		&:first-child:after{
			display:none;
		}

	}
}


.callmenow{
	text-align: right;
	color:$orange-menu;
	font-size:1.5em;
	font-weight: bold;
	font-family: $displayFontFamily;
	float:right;
}
.slogan {
	text-align: center;
	color:rgba($brown-slogan,0.72);
	//@include text-shadow();
	font-size:1.75em;
	line-height:1.125em;
	font-family:$scriptFontFamily;
	margin-left:230px;
	@include breakpoint($desktopBreak) {
		margin-left:155px;
	}

	@include breakpoint($tabletBreak) {
		display:none;
	}
}


.logo{
	position:absolute;
	top:0;
	height:145px;
	width:230px;
	z-index:100;
	@include breakpoint($desktopBreak) {
		width:155px;
		height:auto;
	}
	@include breakpoint($tabletBreak) {
		display:none;
	}
}
.logotext{
	display:none;
	text-indent: 100%;
	white-space: nowrap;
	overflow:hidden;
	zoom:1;
	width:130px;
	height:40px;
	background:url(../images/logo.png) no-repeat center top transparent;
	background-size: 110%;
	@include breakpoint($desktopBreak) {
		display:block;

	}
	// @include breakpoint($tabletBreak) {
	// 	width:301px;
	// 	background:url(../images/logo_tablet.png) no-repeat left center transparent;
	// 	@include background-size();

	// }
	@include breakpoint($tabletBreak) {
		width:165px;
		//height:31px;
		background:url(../images/logo_mobile.png) no-repeat left 3px transparent;


	}
	@include breakpoint($mobileBreak) {
		width:165px;
		//height:31px;
		background:url(../images/logo_mobile.png) no-repeat left 3px transparent;

	}

}